.wrapper {
  width: 100%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  display: grid;
  place-items: center;
  grid-gap: 15px;
  gap: 15px;
}

.marginright {
  margin-right: "100px";
}

.nitrocontainer {
  height: 400px;
  width: 200px;
  border-radius: 8px;
  position: relative;
  cursor: pointer;
  color: #ffffff;
  background: radial-gradient(var(--theme-color-1), var(--theme-color-2));
  /* box-shadow: 0px 1px 10px #ffffff; */
}

.nftcontainer {
  height: 230px;
  width: 100px;
  border-radius: 8px;
